h1 {
  font-size: 2rem;
  line-height: 1.23;
  font-weight: 200;
  letter-spacing: 0.01em;
  @include media-query(phone-only) {
    font-size: 1rem;
  }
}

h2{
  font-weight: 400;
}

h3{
  font-weight: 400;
}
h4{
  font-weight: 700;
}

h5{
  font-weight: bold;
}

h6{
  font-weight: bold;

}
p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.9;
  margin: 10px 0 20px;
  letter-spacing: -0.01em;
}


blockquote{
  p{
    margin-bottom: 10px;
  }
  footer{
    display: block;
    font-size: 80%;
    color: #6c757d;
    &::before{
      content: "\2014\00A0";
    }
  }
}